html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-size: 16px;
}

body:before {
  content: "";
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100vh;
  background-image: url(/assets/header.jpg);
  background-position: 80% center;
  background-repeat: no-repeat;
  background-size:cover;
  pointer-events: none;
}

body {
  font-family: 'Avenir','Helvetica Neue','Helvetica','Arial','Hiragino Sans','\30D2\30E9\30AE\30CE\89D2\30B4\30B7\30C3\30AF','YuGothic','Yu Gothic','\30E1\30A4\30EA\30AA','Meiryo','\FF2D\FF33   \FF30\30B4\30B7\30C3\30AF','MS PGothic' !important;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 700;
}

.bg-dark, a.bg-dark:focus, a.bg-dark:hover, button.bg-dark:focus, button.bg-dark:hover {
  background-color: rgba(0, 0, 0, 0.8) !important;
}

.bg-light, a.bg-light:focus, a.bg-light:hover, button.bg-light:focus, button.bg-light:hover {
  background-color: rgba(255, 255, 255, 0.8) !important;
}

.bg-black, a.bg-black:focus, a.bg-black:hover, button.bg-black:focus, button.bg-black:hover {
  background-color: #000 !important;
}

.bg-gold, a.bg-gold:focus, a.bg-gold:hover, button.bg-gold:focus, button.bg-gold:hover {
  background-color: #ecb807 !important;
}

.text-black, a.text-black:focus, a.text-black:hover, button.text-black:focus, button.text-black:hover {
  color: #000 !important;
}

.text-gold, a.text-gold:focus, a.text-gold:hover, button.text-gold:focus, button.text-gold:hover {
  color: #ecb807 !important;
}

section {
  padding-top: 7.5rem;
  padding-bottom: 7.5rem;
}

section h2 {
  margin-bottom: 3rem;
  font-size: 3rem;
}

section h3 {
  margin-bottom: 0rem;
  font-size: 1rem;
}

.masthead {
  position: relative;
  width: 100vw;
}

/* Side Menu */
#sidebar-wrapper {
  position: fixed;
  z-index: 2;
  right: 0;
  width: 250px;
  height: 100%;
  border-left: 1px solid #ecb807;
  -webkit-transition: all 200ms ease-out 0ms;
  transition: all 200ms ease-out 0ms;
  -webkit-transform: translateX(250px);
  transform: translateX(250px);
}

.sidebar-nav {
  position: absolute;
  top: 0;
  width: 250px;
  margin: 0;
  padding: 0;
  list-style: none;
}

.sidebar-nav li.sidebar-nav-item a {
  display: block;
  text-decoration: none;
  padding: 15px;
}

.sidebar-nav li a:hover,
.sidebar-nav li a:active,
.sidebar-nav li a:focus {
  text-decoration: none;
}

@media (min-width: 992px) {
  .sidebar-nav li a:hover {
    opacity: 0.5;
  }
}

.sidebar-nav > .sidebar-brand {
  font-size: 1.2rem;
  height: 80px;
  line-height: 50px;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 15px;
}

.sidebar-nav > .sidebar-brand a:hover {
  background: none;
}

#sidebar-wrapper.active {
  right: 250px;
  width: 250px;
  -webkit-transition: all 200ms ease-out 0s;
  transition: all 200ms ease-out 0s;
}

.menu-toggle {
  position: fixed;
  right: 15px;
  top: 15px;
  width: 50px;
  height: 50px;
  text-align: center;
  line-height: 50px;
  z-index: 999;
  opacity: 0.75;
}

@-webkit-keyframes serviceIconAnime {
  0% {
    -webkit-transform: scale(1.0);
            transform: scale(1.0);
  }
  100% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
}

@keyframes serviceIconAnime {
  0% {
    -webkit-transform: scale(1.0);
            transform: scale(1.0);
  }
  100% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
}

.service-icon {
  display: block;
  width: 7rem;
  height: 7rem;
  line-height: 7.5rem;
  font-size: 2.25rem;
  box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 0.1);
  -webkit-animation: serviceIconAnime 750ms ease-in-out infinite alternate;
          animation: serviceIconAnime 750ms ease-in-out infinite alternate;
}

.portfolio-item {
  display: block;
  position: relative;
  overflow: hidden;
  max-width: 530px;
  margin: auto auto 1rem;
}

.portfolio-item .caption {
  position: absolute;
  width: 100%;
  bottom: 0;
  left: 0;
}

.portfolio-item .caption h2 {
  text-align: left;
  margin-bottom: 1rem;
  margin-left: 1rem;
  font-size: 1rem;
}

.portfolio-item .caption p {
  font-weight: 300;
  font-size: 1.2rem;
}

footer {
  padding-top: 5rem;
  padding-bottom: 5rem;
}

footer .social-link {
  display: block;
  width: 4rem;
  height: 4rem;
  line-height: 4.3rem;
  font-size: 1.5rem;
  -webkit-transition: transform 100ms ease-out;
  -webkit-transition: -webkit-transform 100ms ease-out;
  transition: -webkit-transform 100ms ease-out;
  transition: transform 100ms ease-out;
  transition: transform 100ms ease-out, -webkit-transform 100ms ease-out;
  box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 0.1);
  text-decoration: none;
}

@media (min-width: 992px) {
  footer .social-link:hover {
    -webkit-transform-origin: center center;
            transform-origin: center center;
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
}

#header {
  background-repeat: no-repeat;
  background-position: 80% center;
  background-size: cover;
}

.header-logo {
  display: block;
  position: absolute;
  top: 25vh;
  width: 100%;
  height: 50vh;
  background-image: url(/assets/logo.png);
  background-repeat: no-repeat;
  background-position: center center;
  background-color: rgba(0, 0, 0, 0.5);
  background-size: contain;
}

@media (min-width: 992px) {
  .header-logo {
    -webkit-transition: transform 100ms ease-out;
    -webkit-transition: -webkit-transform 100ms ease-out;
    transition: -webkit-transform 100ms ease-out;
    transition: transform 100ms ease-out;
    transition: transform 100ms ease-out, -webkit-transform 100ms ease-out;
  }

  .header-logo:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
}

.portfolio-item .caption .caption-content h2 {
  -webkit-transform: translateY(30px);
          transform: translateY(30px);
}

#map {
  width: 100%;
  opacity: 0.9;
}

